$Purple: hsl(259, 100%, 65%);
$Light-red: hsl(0, 100%, 67%);
$White: hsl(0, 0%, 100%);
$Off-white: hsl(0, 0%, 94%);
$Light-grey: hsl(0, 0%, 86%);
$Smokey-grey: hsl(0, 1%, 44%);
$Off-black: hsl(0, 0%, 8%);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-size: 62.5%;
}
body {
  font-family: "Poppins", sans-serif;
  background-color: $Off-white;
  display: flex;
  justify-content: center;
  align-items: center;
  align-self: inherit;
  min-height: 100vh;
  height: 100%;
  width: 100%;
}
.calculator {
  margin: 1rem;
  padding: 4rem 0.5rem;
  width: 95%;
  border-radius: 20px;
  border-bottom-right-radius: 10rem;
  background-color: $White;

  &__up {
    padding-bottom: 5em;
    margin-bottom: 5rem;
    border-bottom: 1px solid $Light-grey;
    &-input {
      margin: 1rem;
      border: 1px solid $Light-grey;
      border-radius: 3px;
      height: 4rem;
      width: 7rem;
      padding-left: 0.5rem;
      font-size: 2rem;
      text-align: center;
      font-weight: bold;
      color: $Off-black;
      transition: border 0.3s;
      cursor: pointer;
      &-error {
        border: 1px solid $Light-red;
      }
      &:hover {
        border: 1px solid $Purple;
      }
      &:focus {
        outline: none;
        border: 1px solid $Purple;
      }
    }

    &-form {
      position: relative;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      &-error {
        display: none;
        margin: 1rem;
        width: 7.5rem;
        font-size: 1.4rem;
        font-style: italic;
        color: $Light-red;
      }
      &-label {
        margin: 1rem;
        margin-bottom: 0;
        text-transform: uppercase;
        font-weight: bold;
        font-size: 1.6rem;
        color: $Smokey-grey;
        &-error {
          color: $Light-red;
        }
      }
      &-submit {
        position: absolute;
        top: 11rem;
        padding: 2rem;
        border: none;
        border-radius: 50%;
        background-color: $Purple;
        cursor: pointer;
        transition: background-color 0.3s;
        &:hover {
          background-color: $Off-black;
        }
        &:focus {
          outline: none;
          background-color: $Off-black;
        }
        &-icon {
          height: 1.5em;
        }
      }

      &-controls {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        width: 100%;
      }
      &-control {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
      }
    }
  }
  &__down {
    &-box {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      margin-left: 2rem;
    }
    &-text {
      font-size: 4.5rem;
      font-weight: bold;
      font-style: italic;
    }
    &-span {
      color: $Purple;
    }
  }
}
.top {
  top: 17rem;
}
@media (min-width: 450px) {
  .calculator {
    width: 45rem;
    &__down {
      &-box {
        margin-left: 6rem;
      }
    }
  }
}
@media (min-width: 992px) {
  .calculator {
    padding: 7rem 3rem;
    width: 70rem;
    &__up {
      &-input {
        width: 12rem;
        height: 6rem;
        font-size: 3.5rem;
      }
      &-form {
        &-controls {
          justify-content: start;
        }
        &-control {
          margin: 0 3rem;
        }
        &-label {
          font-size: 2.3rem;
        }
        &-submit {
          top: 14rem;
          right: 0rem;
        }
        &-error {
          font-size: 1.4rem;
        }
      }
    }
    &__down {
      &-box {
        margin-left: 4rem;
      }
      &-text {
        font-size: 6rem;
      }
    }
  }
  .top {
    top: 20rem;
  }
}
